import tuiCountdown from "@/components/countdown/countdown"
export default {
	components:{
		tuiCountdown
	},
	data() {
		return {
			width:'50%',//进度条的长度
			tabbar:[{
				time:'5:00',
				state:'已结束'
			},{
				time:'5:00',
				state:'已结束'
			},{
				time:'5:00',
				state:'已结束'
			},{
				time:'5:00',
				state:'已结束'
			},{
				time:'5:00',
				state:'已结束'
			},{
				time:'5:00',
				state:'已结束'
			}],
			winHeight: "", //窗口高度
			currentTab: 0, //预设当前项的值
			scrollLeft: 0 ,//tab标题的滚动条位置
			timeSeckillList:[{
				image:'../../../static/demo1.png',
				title:'棉拖鞋秋冬女厚底拖鞋家居款棉拖鞋秋冬女厚底拖鞋家居款',
				width:80,
				sales:1,
				price:21,
				ot_price:12
			},{
				image:'../../../static/demo1.png',
				title:'棉拖鞋秋冬女厚底拖鞋家居款棉拖鞋秋冬女厚底拖鞋家居款',
				width:80,
				sales:1,
				price:21,
				ot_price:12
			},{
				image:'../../../static/demo1.png',
				title:'棉拖鞋秋冬女厚底拖鞋家居款棉拖鞋秋冬女厚底拖鞋家居款',
				width:80,
				sales:1,
				price:21,
				ot_price:12
			}],//秒杀商品列表
			typeTime:1,//当前状态
			timeList: [1000, 2000, 3000, 19, 240000],//倒计时
			loadingPlan:80,
		}
	},
	onLoad: function() {
		let that = this;
		//  高度自适应
		uni.getSystemInfo({
			success: function(res) {
				let calc = res.windowHeight;
				that.winHeight = calc;
			}
		});
	},
	methods: {
		
		
		//秒杀详情
		timeSeckillClick(key){
			uni.navigateTo({
				url:'../../ShopDetails/seckillTime'
			})
		},
		//切换时间
		timeswitching(){
			setTimeout(function(){
				uni.hideLoading()
			},1500)
		},
		// 滚动切换标签样式
		switchTab: function(e) {
			let that = this;
			that.currentTab = e.detail.current;
			that.checkCor();
			uni.showLoading({title:'正在加载'})
			setTimeout(function(){
				that.timeswitching();
			},500)
		},
		// 点击标题切换当前页时改变样式
		swichNav: function(e) {
			let cur = e.currentTarget.dataset.current;
			if (this.currentTab == cur) {
				return false;
			} else {
				this.currentTab = cur
			}
		},
		//判断当前滚动超过一屏时，设置tab标题滚动条。
		checkCor: function() {
			if (this.currentTab > 3) {
				//这里距离按实际计算
				this.scrollLeft = 300
			} else {
				this.scrollLeft = 0
			}
		},
		detail(e) {
			uni.navigateTo({
				url: '../extend-view/newsDetail/newsDetail'
			})
		}
	}
}
